  margin: 0;
  padding: 0;
}

body {
  display: flex;

  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

#greeting,
#title {
  font-size: 80px;
  font-weight: 800;
  margin-top: 10px;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
#newGame {
  font-size: 20px;
  padding: 8px;
  border-radius: 12px;
  border: none;
  position: relative;
  top: 300px;
}
#newGame:hover {
  transform: scale(1.2);
}

#gatherPlayer1Info,
#gatherPlayer2Info {
  display: none;
  font-size: 70px;
  font-weight: 900;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
#player1 {
  font-size: 30px;
  background-color: rgba(255, 255, 255, 0.4);
}
#player2 {
  font-size: 30px;
  background-color: rgba(255, 255, 255, 0.4);
}
#vs {
  display: none;
  font-size: 70px;
  font-weight: 900;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
#startGame {
  display: none;
  position: relative;
  left: 45%;
  top: 20px;
  border-radius: 12px;
  width: 150px;
  font-size: 30px;
  background-color: rgba(134, 133, 133, 0.4);
  border: none;
  padding: 8px;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
.opponentContainer {
  display: flex;
  flex-direction: row;
  gap: 40px;
  justify-content: center;
}
.opponent {
  width: 150px;
  font-size: 30px;
  background-color: rgba(134, 133, 133, 0.4);
  border: none;
  padding: 8px;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
.opponent:hover {
  transform: scale(1.2);
}

#placeMessage {
  display: flex;
  flex-direction: column;
  font-size: 40px;
  width: 590px;
  left: 20px;
  justify-content: center;
  align-items: center;
  border: none;
  padding: 8px;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}

.boardContainer {
  display: none;
  flex-direction: row-reverse;

  justify-content: center;
  height: auto;
}
.zoom-out {
  animation: zoomOut 0.5s forwards;
}

.zoom-in {
  animation: zoomIn 0.5s forwards;
}

@keyframes zoomOut {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(0);
  }
}

@keyframes zoomIn {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

#gameBoard,
#gameBoard2 {
  display: grid;
  justify-self: center;
  align-items: center;
  grid-template-columns: repeat(10, minmax(30px, 1fr));
  grid-template-rows: repeat(10, minmax(30px, 1fr));
  width: 540px;
  height: 540px;
}
#numberContainer {
  display: flex;
  /* text-align: center; */
  flex-direction: row;
  justify-content: center;
  font-size: 2rem;
  gap: 5px;

  padding-top: 20px;
  /* width: 540px; */
  margin: 0px;
}
#numberContainer {
  font-size: 2rem;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
#letterContainer {
  grid-column: 1/2;
  grid-row: 1/3;
  display: flex;
  margin-top: 5px;
  gap: 5px;
  flex-direction: column;
  font-size: 2rem;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}
.number,
.letter {
  width: 50px;
  height: 50px;
}
#placeShipButtons {
  display: flex;
  gap: 20px;
}
#gameOverButtons {
  display: flex;
  flex-direction: row;
  gap: 50px;
}
#readyButton,
#orientation,
#playAgain,
#restartButton,
#randomButton {
  display: flex;
  position: relative;
  justify-content: center;

  width: 150px;
  font-size: 30px;
  background-color: rgba(134, 133, 133, 0.4);
  border-radius: 12px;
  border: none;
  padding: 8px;
  color: rgba(255, 255, 255, 1);
  text-shadow: 2px 2px 0px rgb(0, 0, 0), 2px -2px 0px rgb(0, 0, 0),
    -2px 2px 0px rgb(0, 0, 0), -2px -2px 0px rgb(0, 0, 0),
    2px 0px 0px rgb(0, 0, 0), 0px 2px 0px rgb(0, 0, 0),
    -2px 0px 0px rgb(0, 0, 0), 0px -2px 0px rgb(0, 0, 0);
}

.boardSquare {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  background-color: aqua;
  border: solid black 2px;
}
.highlight {
  background-color: #ffff99; /* Light yellow */
}
@keyframes hitAnimation {
  0% {
    background-color: red;
    transform: scale(1);
  }
  50% {
    background-color: orange;
    transform: scale(1.1);
  }
  100% {
    background-color: red;
    transform: scale(1);
  }
}

.hit {
  color: black;
  animation: hitAnimation 1s ease-in-out;

  background-color: red;
}
.hasBeenHit {
  color: black;
  background-color: red;
}
@keyframes missAnimation {
  0%,
  100% {
    background-color: transparent;
  }
  50% {
    background-color: cadetblue; /* Or any color you prefer */
  }
}

/* Apply the animation to the miss class */
.miss {
  animation: missAnimation 2s ease-in-out;
  /* Optional: Customize the appearance of the miss square */
  color: #fffefe; /* Text color */
  text-align: center;
  line-height: 2; /* Adjust based on your square size to vertically center the text */
}
.hasBeenMissed {
  color: white;
  background-color: cadetblue;
}

.shipHasBeenSunk {
  background-color: darkred;
  font-size: 30px;
}
